import axios from "axios";

export const Ecode = (n, clbk) => {
    // console.log("n" + n);
    var data = {
        str: n
    }
    axios.post("/encode", data).then(res => {
        //console.log(res.data);
        clbk(res.data);
    })
}
export const Dcode = (n, clbk) => {
    // console.log("n" + n);
    var data = {
        str: n
    }
    axios.post("/decode", data).then(res => {
        //console.log(res.data);
        clbk(res.data);
    })
}