import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import { fadeInDownBig, fadeInRight, fadeInLeft } from 'react-animations'
import { Ecode, Dcode } from "./apiCall/call";
import './App.css';

const Bounce = styled.div`animation: 2s ${keyframes`${fadeInDownBig}`} `;
const Left = styled.div`animation: 2s ${keyframes`${fadeInLeft}`} `;
const Right = styled.div`animation: 2s ${keyframes`${fadeInRight}`} `;

function App() {
  const [str1, setStr1] = useState("");
  const [str2, setStr2] = useState("");
  const [ans, setAns] = useState("");
  const [err, setErr] = useState("");
  function submitE(e) {
    e.preventDefault();
    // var data=
    setAns("");
    setErr("");
    Ecode(str1, (data) => {
      if (data == "-1")
        setErr("Enter only Lower case"); else if (data == "-2") setErr("No numbers allowed"); else setAns(data);
    })
  }
  function submitD(e) {
    e.preventDefault();
    // var data=
    setAns("");
    setErr("");
    Dcode(str2, (data) => {
      setAns(data);
    })
  }
  function encodeData(e) {
    //console.log(e.target)
    var temp = e.target.value;
    setStr1(temp);
    //console.log(str1);
  }
  function decodeData(e) {
    setStr2(e.target.value);
  }
  return (
    <div>
      <div className="flex"><Bounce><h1>Encode/Decode!</h1></Bounce></div>
      <br />
      <br />
      <form className="form-group " noValidate >
        <div className="flex" style={{ justifyContent: "space-around" }}>
          <Left > <input type="text" placeholder="Enter plain text" name="enc" onChange={encodeData} />
            <input type="submit" value="Encode" className="btn btn-primary" onClick={submitE} /></Left>


          <Right> <input type="text" placeholder="Enter the encoded text" name="dec" onChange={decodeData} />
            <input type="submit" value="Decode" className="btn btn-success" onClick={submitD} /></Right>
        </div>
      </form>
      <div className="flex"> {ans ? <h3>Answer is: {ans} !</h3> : null}
        {err ? <small style={{ color: "red" }}>{err}</small> : null}
      </div>

    </div>
  );
}

export default App;
